:root{
--bgColor: var(--darkBgColor);
--cardBgColor: var(--darkCardBgColor);
--scoreboardColor: var(--darkScoreboardColor);
--scoreboardSecondary: var(--darkScoreboardSecondary);
--scoreboardThird: var(--darkScoreboardThird);

--darkBgColor: rgb(35, 35, 35);
--darkCardBgColor: rgb(43, 43, 43);
--darkScoreboardColor: rgba(0, 0, 0, 0.233);
--darkScoreboardSecondary: rgb(29, 27, 27);
--darkScoreboardThird: rgb(0, 0, 0);

--lightBgColor: rgb(255, 255, 255);
--lightCardBgColor: rgb(214, 214, 214);
--lightScoreboardColor: rgb(255, 253, 253);
--lightScoreboardSecondary: rgb(255, 255, 255);
--lightScoreboardThird: rgb(253, 253, 253);
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--bgColor);
  height: 100vh;
  font-family: "Anton", sans-serif;
  overflow: hidden;

}
#light-dark-button{
  background-color: rgba(0, 0, 0, 0.0);
  border: none;
  position: fixed;
  top: 2vh;
  right: 20vw;
  white-space: pre-wrap; 
z-index: 2;
}
#scoreboard{
  display:flex;
  flex-direction: column;
  font-size: 1rem;
  border: 1px solid var(--scoreboardSecondary);
  top: 10vh;
  box-shadow: inset 1px 1px 33px var(--scoreboardThird);
  border-radius: 5%;
  margin-top: 10px;
  height: 25vh;
}
#score-top{
  background-color: var(--scoreboardColor);
  height: 5vh;
  line-height: 1.3;
  position: relative;
  top: -2.5vh;
  border-radius: 5%;
  
}
#score-bottom{
  background-color: var(--scoreboardColor);
  height: 5vh;
  line-height: 1.3;
  position: relative;
  top: 2vh;
  border-radius: 5%;

}
.number{
  font-size: 4rem;
  line-height: .5;
  margin-top: 10px;
  position: relative;
  top: -1.5vh;
  height: 10vh;
}
.header {
  background-color: var(--bgColor);
  height: 7vh;
 width: 100vw;
  font-size: calc(10px + 8vmin);
  color: var(--bgColor);
  position: fixed;
  top: 0;
  left: 0vw;
  text-align: left;
  text-indent: 3%;
}
#add-a-card {
  white-space: pre-wrap; 
  position: fixed;
  top: 1vh;
  right: 1vw;
  background-color: rgba(0, 0, 0, 0.027);
  border-radius: 5%;
  height: 7vh;
  width: 20vw;
  font-size: 1rem;
  border: none;
  box-shadow: 1px 1px 2px black;
  min-height: 40px;
}
#footer {
  position: fixed;
  bottom: 0vh;
}
.textInputs{
  margin: 10px;
  background-color: rgba(0, 0, 0, 0.027);
color: rgb(145, 144, 144);
}
.decks{
  width: 25vw;
  height: 5vh;
  background-color: transparent;
  font-family: "Anton", sans-serif;
  border: none;
  border-radius: 3%;
}
#tally-minus-button, #tally-plus-button{
  background-color: rgba(0, 0, 0, 0.027);
  border: none;
  font-size: 3rem;
  position: relative;
  width: 15vw;
}
#tally-plus-button{
  top: -1vh;

}
#tally-minus-button{
bottom: -1vh;
}
#form{
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#exit-button{
  position: relative;
  top: -12vh;
  background-color: rgba(0, 0, 0, 0.027);
  color: rgb(145, 144, 144);

}
#save-button{
  position: relative;
  bottom: -12vh;
  background-color: rgba(0, 0, 0, 0.027);
  color: rgb(145, 144, 144);
}
#card-container{
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  height: 77vh;
  max-height: 900px;
  position: relative;
  top: 1.5vh;
  left: 0vw;
  width: 96vw;
  max-width: 1200px;
  border-radius: 1%;
  box-shadow: 1px 1px 6px rgba(10, 10, 10, 0.5);
  letter-spacing: .2ch;
  background-color: var(--cardBgColor);
}
p{
  line-height: .1;
  font-size: calc(1px + 3vmin);

}
#prev-button{
  background-color: rgba(0, 0, 0, 0.027);
  position: fixed;
  bottom: 12vh;
  left: 5vw;
  border: none;

}
#next-button{
  background-color: rgba(0, 0, 0, 0.027);
  position: fixed;
  bottom: 12vh;
  right: 5vw;
  border: none;

}
#edit-button{
  background-color: rgba(0, 0, 0, 0.027);
  border: none;
  position: fixed;
  bottom: 18vh;
  left: 5vw;
}

@keyframes fadeIn{
  from {
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}
@keyframes slideLeft{
  from {
    left: 0vw;
  }
  to{
    left: -350vw;
  }
}
@keyframes slideRight{
  from {
    left: 0vw;
  }
  to{
    left: 350vw;
  }
}
/* app.css */

/* Other CSS rules */

@media screen and (orientation: portrait) and (max-width: 400px) {
  /* Your CSS rules specific to devices in portrait orientation */
  #card-container{
    top: 1.4vh;
    height: 85vh;
  }
  #prev-button{
    bottom: 8vh;
    left: 6vw;
  }
  #next-button{
    bottom: 8vh;
    right: 6vw;
  }
  #tally-plus-button{
    top: -5vh;
  
  }
  #tally-minus-button{
  bottom: -5vh;
  }
  #scoreboard{
    height: 28vh;
    position: relative;
    top: 2vh;
  }
  #score-top{
    height: 5vh;
    line-height: 1.3;
    top: -3.2vh;
    
  }
  #score-bottom{
    height: 5vh;
    line-height: 1.3;
    top: 2.8vh;
  }
  p{
    line-height: .1;
    font-size: calc(4px + 6vmin);
  
  }
}
@media screen and (orientation: landscape) and (max-height: 450px) {
  #tally-plus-button{
    top: 2vh;
  
  }
  #score-bottom{
    opacity: 0;
  }
  #tally-minus-button{
  bottom: 1vh;
  }
}
